import styled from "@emotion/styled";
import colors from "styles/colors";
import { padding } from "polished";

export const Size = {
  LARGE: {
    inputFontSize: 14,
    errorFontSize: 12,
    height: 60,
    marginBottom: 16,
  },
  MEDIUM: {
    inputFontSize: 12,
    errorFontSize: 10,
    height: 40,
    marginBottom: 8,
  },
};

export const Input = styled("input")(({ error }) => [
  {
    width: "100%",
    ...padding(0, 10),
    border: `1px solid ${colors.lightGray}`,
    borderRadius: 0,
    outlineRadius: 0,
    fontWeight: 500,
    "&::placeholder": {
      color: colors.black,
      fontWeight: 300,
    },
    "&:focus": {
      borderColor: colors.gray1,
      outlineColor: colors.gray1,
      outlineStyle: "solid",
      outlineWidth: 1,
    },
  },
  error && {
    border: `1px solid ${colors.warning}`,
    "&:focus": {
      borderColor: colors.warning,
      outlineColor: colors.warning,
      outlineStyle: "solid",
      outlineWidth: 1,
    },
  },
]);

export const InputError = styled("span")(({ fontSize }) => ({
  position: "absolute",
  fontSize,
  color: colors.warning,
  right: 40,
  display: "flex",
  alignItems: "center",
  height: "100%",
}));

export const InputWrapper = styled("div")(({ type, size }) => [
  {
    position: "relative",
    width: "100%",
    height: size.height,
    fontSize: size.inputFontSize,
    marginBottom: size.marginBottom,
    display: "flex",
  },

  type === "hidden" && {
    display: "none",
  },
]);

export const FlagIcon = styled("div")({
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  width: 20,
  height: "100%",
  position: "absolute",
  right: 10,
});
