import React from "react";

import { InputError, InputWrapper, Input, Size, FlagIcon } from "./elements";

export { Size } from "./elements";

const getFlag = (value, countryCode) => {
  if (!value) {
    return null;
  }

  return countryCode.replace(/./g, (char) =>
    String.fromCodePoint(char.charCodeAt(0) + 127397),
  );
};

const EnhancedPhoneInput = ({
  autoFocus,
  name,
  placeholder,
  onBlur,
  onChange,
  country,
  value,
  error,
  touched,
  id,
  size = Size.LARGE,
}) => (
  <InputWrapper size={size}>
    <Input
      type="tel"
      name={name}
      placeholder={placeholder}
      onBlur={onBlur}
      onChange={onChange}
      value={value}
      id={id}
      size={size}
      autoFocus={autoFocus}
      error={touched && error}
    />
    {!!error && touched && (
      <InputError fontSize={size.errorFontSize}>{error}</InputError>
    )}
    <FlagIcon>{getFlag(value, country)}</FlagIcon>
  </InputWrapper>
);

export default EnhancedPhoneInput;
