import { reduce } from "lodash";

export const constructEmailBody = (values, questions) => {
  const labels = questions.map(({ customerCopy }) => customerCopy);

  const conditionalNewLine = (message) => (message.length === 0 ? "" : "\n\n");

  const portalAnswers = reduce(
    labels,
    (message, label, index) => {
      const answerList = questions[index].answers
        .filter((answer) => answer.selected)
        .map((answer) => answer.displayTerm)
        .join(", ");
      return answerList
        ? `${message}${conditionalNewLine(message)}${label}: ${answerList}.`
        : message;
    },
    "",
  );

  return `${values.fullName} from ${values.companyName}${
    values.industry ? ` (${values.industry})` : ""
  } has expressed interest in your services${
    values.message ? `:\n\n"${values.message}"` : "."
  }

${portalAnswers}${
    values.phoneNumber ? `\n\nPhone number: ${values.phoneNumber.number}.` : ""
  }

Reply to this email to respond to ${values.fullName}.
`;
};
