import React from "react";
import { compose, lifecycle, withStateHandlers, withHandlers } from "recompose";
import * as contentful from "contentful";
import { cloneDeep } from "lodash";
import * as r from "ramda";

import { Column, Row } from "components/Grid.tsx";
import Layout from "components/layout";
import Times from "assets/img/times.svg";
import PartnerCard from "components/portal/PartnerCard";
import QuestionPlatform from "components/portal/QuestionPlatform";
import ControlBar from "components/portal/ControlBar";

import {
  PartnerContainer,
  HeaderWrapper,
  RemoveIcon,
  _SelectedAnswer,
  PartnersTitle,
  TitleWrapper,
  PartnerList,
} from "components/portal/elements";

import {
  indexSearchEngine,
  getPartners,
  getQuestions,
  applyCustomSearchLogic,
} from "components/portal/agencySearch";

const SelectedAnswer = ({ handler, children }) => (
  <_SelectedAnswer>
    {children}
    <RemoveIcon src={Times} onClick={handler} />
  </_SelectedAnswer>
);

const mapIndexed = r.addIndex(r.map);

const getSelectedAnswers = (questions) =>
  r.pipe(
    mapIndexed((question, index) => ({
      answers: question.answers,
      questionIndex: index,
      deletableAnswer: question.deletableAnswer,
    })),
    r.map((question) =>
      mapIndexed((answer, index) => ({
        ...answer,
        deletableAnswer: question.deletableAnswer,
        answerIndex: index,
        questionIndex: question.questionIndex,
      }))(question.answers),
    ),
    r.flatten,
    r.filter((answer) => answer.selected),
    r.sort((a, b) => a.timestamp - b.timestamp),
  )(questions);

const shouldShowQuestion = (questionIndex, questions) => {
  const dependentTerms = questions[questionIndex].answerDependencySearchTerms;
  if (!dependentTerms) return true;
  const selectedAnswers = getSelectedAnswers(questions);
  return r.pipe(
    r.map((answer) => answer.searchTerm),
    r.intersection(dependentTerms),
    (result) => result.length > 0,
  )(selectedAnswers);
};

const findSelectedAnswer = (answers) =>
  r.pipe(
    r.find(r.propEq("selected", true)),
    (answer) => answer && answer.searchTerm,
  )(answers);

const createQuery = (questions) =>
  r.pipe(
    r.map((question) => question.answers),
    r.flatten,
    r.filter((answer) => answer.selected),
    r.map(
      (answer) =>
        `${answer.requiredMatch ? "+" : ""}${answer.searchTerm}^${
          answer.weight
        }`,
    ),
    r.flatten,
    (terms) => terms.join(" "),
  )(questions);

const reweightQuery = (query) => {
  // If SYSTEMS_INTEGRATION or PRODUCT_DEVELOPMENT are selected, rejigger
  // weights directly onto concatenated query string.
  // NOTE: This assumes a particular ranking scheme, where the first
  // question is ranked 5 and rank is decremented by 1 for each subsequent
  // question.
  if (
    /AGENCY__1__(SYSTEMS_INTEGRATION|PRODUCT_DEVELOPMENT|SOFTWARE_SETUP)/.test(
      query,
    )
  ) {
    return query
      .replace("^3", "^@")
      .replace("^4", "^3") // Assumes that ranks shift down
      .replace("^2", "^4") // Move software question from 2nd highest rank
      .replace("^@", "^2");
  }
  return query;
};

// set query params based on selected answers
const setQueryParams = (questions) => {
  const selectedAnswers = getSelectedAnswers(questions);
  const queryParams = new URLSearchParams();

  selectedAnswers.forEach((answer) => {
    const question = questions[answer.questionIndex];
    if (queryParams.has(question.id)) {
      const oldValue = queryParams.get(question.id);
      queryParams.set(question.id, `${oldValue},${answer.answerIndex}`);
    } else {
      queryParams.append(question.id, answer.answerIndex);
    }
  });

  const newPath = `${window.location.pathname}?${queryParams}${window.location.hash}`;
  window.history.replaceState(null, "", newPath);
};

const Partners = ({
  searchResults,
  partners,
  questions,
  currentQuestion,
  goToPreviousQuestion,
  goToNextQuestion,
  handleAnswerSelection,
  location,
  setIsFinished,
  isFinished,
}) => {
  const selectedAnswers = getSelectedAnswers(questions);
  const multipleAnswers =
    !!questions[currentQuestion] && questions[currentQuestion].multipleAnswers;

  const renderSelectedAnswers = () =>
    getSelectedAnswers(questions).map((answer) => (
      <React.Fragment key={answer.displayTerm}>
        <SelectedAnswer
          handler={() =>
            handleAnswerSelection(answer.questionIndex, answer.answerIndex)
          }
        >
          {answer.displayTerm}
        </SelectedAnswer>
      </React.Fragment>
    ));

  return (
    <Layout
      location={location}
      title="The Brex Agency Portal for Businesses | Brex"
      description="From analytics to business strategy, the Brex Portal has a Partner for all of your business's consulting needs. Discover yours today!"
      hideDefaultFooter={currentQuestion === 0}
      autoScrollBehavior
    >
      {!!questions[currentQuestion] && (
        <HeaderWrapper
          shrink={selectedAnswers.length > 0}
          isFinished={isFinished}
        >
          <QuestionPlatform
            isFinished={isFinished}
            questions={questions}
            currentQuestion={currentQuestion}
            handleAnswerSelection={handleAnswerSelection}
            multipleAnswers={multipleAnswers}
            renderSelectedAnswers={renderSelectedAnswers}
          />
          <ControlBar
            isFinished={isFinished}
            goToPreviousQuestion={goToPreviousQuestion}
            currentQuestion={currentQuestion}
            renderSelectedAnswers={renderSelectedAnswers}
            questions={questions}
            selectedAnswers={selectedAnswers}
            goToNextQuestion={goToNextQuestion}
            setIsFinished={setIsFinished}
          />
        </HeaderWrapper>
      )}
      <PartnerContainer hidden={selectedAnswers.length === 0}>
        <Row padding={30} gutterWidth={26}>
          <Column span={1} spanSmall={0} />
          <Column span={10} spanSmall={12}>
            <TitleWrapper>
              <PartnersTitle>Partners</PartnersTitle>
            </TitleWrapper>
            <PartnerList>
              {!!searchResults &&
                searchResults.map((result) => (
                  <PartnerCard
                    partnerType="Agency"
                    partner={partners[result.ref]}
                    key={partners[result.ref].id}
                    questions={questions}
                  />
                ))}
            </PartnerList>
          </Column>
          <Column span={1} spanSmall={0} />
        </Row>
      </PartnerContainer>
    </Layout>
  );
};

const _initialState = {
  query: "",
  searchResults: [],
  searchEngine: null,
  partners: {},
  _isMounted: false,
  questions: [],
  isFinished: false,
  currentQuestion: 0,
};

const _stateHandlers = {
  setQuery: () => (query) => ({ query }),
  setSearchResults: () => (results) => ({ searchResults: results }),
  setSearchEngine: () => (searchEngine) => ({ searchEngine }),
  setPartners: () => (partners) => ({ partners }),
  setIsMounted: () => (_isMounted) => ({ _isMounted }),
  setQuestions: () => (questions) => ({ questions }),
  setIsFinished: () => (isFinished) => ({ isFinished }),
  setCurrentQuestion: () => (currentQuestion) => {
    window.location.hash = currentQuestion;
    return { currentQuestion };
  },
  goToPreviousQuestion: ({ currentQuestion, questions }) => () => {
    if (currentQuestion === 0) {
      return { currentQuestion };
    }
    let steps = -1;
    while (!shouldShowQuestion(currentQuestion + steps, questions)) {
      steps -= 1;
    }
    if (window.analytics) {
      window.analytics.track("Click: Button", {
        target: `agencyPortal.backButtonClicked.question${questions[currentQuestion].index}`,
      });
    }
    window.scrollTo(0, 0);

    const newCurrentQuestion = currentQuestion + steps;
    window.location.hash = newCurrentQuestion;
    return { currentQuestion: newCurrentQuestion };
  },
  goToNextQuestion: ({ currentQuestion, questions }) => () => {
    if (currentQuestion + 1 > questions.length - 1) {
      return { currentQuestion };
    }
    let steps = 1;
    while (!shouldShowQuestion(currentQuestion + steps, questions)) {
      steps += 1;
    }
    if (window.analytics) {
      window.analytics.track("Click: Button", {
        target: `agencyPortal.forwardButtonClicked.question${questions[currentQuestion].index}`,
      });
    }
    window.scrollTo(0, 0);

    const newCurrentQuestion = currentQuestion + steps;
    window.location.hash = newCurrentQuestion;
    return { currentQuestion: newCurrentQuestion };
  },
};

const _handlers = {
  handleAnswerSelection: ({
    questions,
    setQuestions,
    setQuery,
    searchEngine,
    setSearchResults,
    partners,
    setIsFinished,
    currentQuestion,
    setCurrentQuestion,
  }) => (questionIndex, answerIndex) => {
    // ANSWER SELECTION
    // TODO: replace clonedeep with something more efficient
    const newQuestions = cloneDeep(questions);
    const selectedQuestion = newQuestions[questionIndex];

    if (selectedQuestion.multipleAnswers) {
      selectedQuestion.answers[answerIndex].selected = !questions[questionIndex]
        .answers[answerIndex].selected;
    } else {
      if (selectedQuestion.requireAnswer) {
        // find current selected display term
        const unselectedSearchTerm = findSelectedAnswer(
          selectedQuestion.answers,
        );

        // find index of the question that's dependent
        for (let i = 0; i < questions.length; i += 1) {
          const searchTerms = questions[i].answerDependencySearchTerms;
          if (searchTerms && searchTerms.indexOf(unselectedSearchTerm) > -1) {
            const newAnswers = questions[i].answers.map((answer) => ({
              ...answer,
              selected: false,
            }));
            newQuestions[i].answers = newAnswers;
          }
        }
      }

      selectedQuestion.answers = selectedQuestion.answers.map(
        (answer, index) => ({
          ...answer,
          selected: index === answerIndex && !answer.selected,
        }),
      );
    }

    // if customer x's all I need help with set them back to first question
    if (
      questionIndex === 0 &&
      selectedQuestion.answers.filter((answer) => answer.selected).length === 0
    ) {
      setCurrentQuestion(0);
    }

    // if customer customer x's company type while on company size set them back
    // to what type company question
    if (
      questionIndex === 1 &&
      (currentQuestion === 2 || currentQuestion === 3) &&
      selectedQuestion.answers.filter((answer) => answer.selected).length === 0
    ) {
      setCurrentQuestion(1);
    }

    if (getSelectedAnswers(newQuestions).length === 0) {
      setIsFinished(false);
      setCurrentQuestion(0);
    }

    selectedQuestion.answers[answerIndex].timestamp = Date.now();
    setQuestions(newQuestions);

    setQueryParams(newQuestions);
    const query = reweightQuery(createQuery(newQuestions));
    setQuery(query);

    const results = searchEngine.search(query);
    const customSearchResults = applyCustomSearchLogic(
      results,
      partners,
      query,
    );
    setSearchResults(customSearchResults);
  },
};

const _lifecycle = {
  componentDidMount() {
    this.props.setIsMounted(true);

    const partnerPortalClient = contentful.createClient({
      /* eslint-disable-next-line no-undef */
      accessToken: brexGlobal.CONTENTFUL_REFERRAL_PORTAL_ACCESS_TOKEN,
      /* eslint-disable-next-line no-undef */
      space: brexGlobal.CONTENTFUL_REFERRAL_PORTAL_SPACE_ID,
    });

    window.client = partnerPortalClient;
    // fetch questions
    const getQuestionsPromise = getQuestions(partnerPortalClient);

    // fetch partners
    const getPartnersPromise = getPartners(partnerPortalClient).then(
      (partners) => {
        const engine = indexSearchEngine(Object.values(partners));
        return { engine, partners };
      },
    );

    Promise.all([getQuestionsPromise, getPartnersPromise]).then(
      ([questions, { engine, partners }]) => {
        window.partners = partners;
        // Done to avoid memory leak: https://reactjs.org/blog/2015/12/16/ismounted-antipattern.html
        if (this.props._isMounted) {
          // set questions
          this.props.setQuestions(questions);

          // read in current question index from hash
          const currentQuestionIndex = parseInt(
            window.location.hash.substring(1),
            10,
          );

          // current question index must be within range
          if (
            currentQuestionIndex >= 0 &&
            currentQuestionIndex < questions.length
          ) {
            // current question index must be valid
            if (shouldShowQuestion(currentQuestionIndex, questions)) {
              this.props.setCurrentQuestion(currentQuestionIndex);
            } else {
              window.location.hash = "";
            }
          }

          // set partners and search engine
          this.props.setPartners(partners);
          this.props.setSearchEngine(engine);

          // load in search results only if answers have been selected via URL
          const query = createQuery(questions);

          if (query.length > 0) {
            this.props.setQuery(query);
            const results = engine.search(query);
            const customSearchResults = applyCustomSearchLogic(
              results,
              partners,
              query,
            );
            this.props.setSearchResults(customSearchResults);
          }
        }
      },
    );
  },
  componentWillUnmount() {
    this.props.setIsMounted(false);
  },
};

export default compose(
  withStateHandlers(_initialState, _stateHandlers),
  withHandlers(_handlers),
  lifecycle(_lifecycle),
)(Partners);
