import { compose, withHandlers, withStateHandlers } from "recompose";

const withOpenClose = ({
  openStateName = "isOpen",
  openHandlerName = "open",
  closeHandlerName = "close",
  toggleHandlerName = "toggle",
  setHandlerName = "set",
  defaultValue = false,
} = {}) =>
  compose(
    withStateHandlers(
      {
        [openStateName]: defaultValue,
      },
      {
        [openHandlerName]: () => () => ({ [openStateName]: true }),
        [closeHandlerName]: () => () => ({ [openStateName]: false }),
        [setHandlerName]: () => (value) => ({ [openStateName]: value }),
      },
    ),
    withHandlers({
      // prettier-ignore
      [toggleHandlerName]: (props) => (
        props[openStateName] ? props[closeHandlerName] : props[openHandlerName]
      )
    }),
  );

export default withOpenClose;
