import React from "react";

import { createBody } from "utils/contentful";

import {
  Main,
  MainDescription,
  List,
  SectionTitle,
  ListItem,
  Bullet,
  Logo,
  Content,
} from "./elements";

const Details = ({ detailedDescription, detailedPoints }) => (
  <Main>
    <MainDescription>{createBody(detailedDescription)}</MainDescription>
    {detailedPoints.map((section) => (
      <React.Fragment key={section.title}>
        <SectionTitle>{section.title}</SectionTitle>
        <List>
          {section.points.map((item, i) => (
            <React.Fragment key={item.key}>
              {section.bullets && (
                <ListItem alignItems="center">
                  {section.bullets[i] ? (
                    <Logo
                      width={section.bullets[i].width || 31}
                      height={section.bullets[i].height || 31}
                      src={section.bullets[i].src}
                      alt="bullet"
                    />
                  ) : (
                    <Bullet marginTop={0} />
                  )}
                  {item.value}
                </ListItem>
              )}
              {!section.bullets && (
                <ListItem>
                  <Bullet />
                  <Content>{item.value}</Content>
                </ListItem>
              )}
            </React.Fragment>
          ))}
        </List>
      </React.Fragment>
    ))}
  </Main>
);

export default Details;
