import React from "react";
import ReactDOM from "react-dom";
import { isEmpty } from "lodash";
import { withFormik } from "formik";
import * as Yup from "yup";
import {
  compose,
  withHandlers,
  branch,
  renderComponent,
  withStateHandlers,
  lifecycle,
} from "recompose";
import {
  AsYouType,
  parsePhoneNumberFromString,
  formatIncompletePhoneNumber,
} from "libphonenumber-js";
import phoneMetadata from "libphonenumber-js/metadata.min.json";

import withOpenClose from "hocs/withOpenClose";
import withPOSTFormSubmission from "hocs/withPOSTFormSubmission";
import EnhancedInput, { Size } from "components/form/EnhancedInput";
import EnhancedPhoneInput, {
  Size as PhoneInputSize,
} from "components/form/EnhancedPhoneInput";
import EnhancedTextArea, {
  Size as TextAreaSize,
} from "components/form/EnhancedTextArea";
import withClickOutside from "react-onclickoutside";
import Times from "assets/img/times.svg";
import ThankYouIcon from "assets/img/portal/thank-you-icon.png";

import {
  Modal,
  MessageContainer,
  Title,
  FormContainer,
  SubmitButton,
  QuitIcon,
  SubmissionIcon,
  Subtitle,
  QuitButton,
  Error,
} from "./elements";
import { constructEmailBody } from "./messages";

const inputFields = [
  {
    name: "fullName",
    placeholder: "Full name",
    type: "freeText",
  },
  {
    name: "workEmail",
    placeholder: "Work email",
    type: "email",
  },
  {
    name: "companyName",
    placeholder: "Company",
    type: "freeText",
  },
  {
    name: "phoneNumber",
    placeholder: "Phone number (optional)",
    type: "phoneNumber",
  },
  {
    name: "industry",
    placeholder: "Industry (optional)",
    type: "freeText",
  },
];

const _ContactForm = ({
  setFieldValue,
  values,
  errors,
  touched,
  handleChange,
  handleBlur,
  isSubmitting,
  submitForm,
  setHasSubmittedForm,
  successfulSubmission,
  failedSubmission,
  partner,
}) => {
  if (successfulSubmission) {
    setHasSubmittedForm(true);
    if (window.analytics) {
      // For client info, just form a period-separated string of the client
      // fields. Replace periods with an arbitrary character to allow
      // splitting by period.
      const clientInfo = ["fullName", "companyName", "workEmail"]
        .map((field) => values[field].replace(".", "|"))
        .join(".");
      const infoString = `${partner.name}.${clientInfo}`;

      // For analytics partner prefix, if partner has a simplified "type"
      // field, use it directly; otherwise get it from its subfields.
      const partnerPrefix = (typeof partner.type === "string"
        ? partner.type
        : partner.type.fields.category
      )
        .split(" ")[0]
        .toLowerCase();

      window.analytics.track("Click: ContactPartner", {
        target: `${partnerPrefix}Portal.partnerContactUsClicked.${infoString}`,
      });
    }
  }

  const processPhoneNumberInput = (newValue, oldValue) => {
    let newNumber = newValue.number;
    const oldCountry = oldValue.country;

    if (newNumber === undefined) {
      return {
        number: "",
        country: oldCountry,
      };
    }

    // parse the new phone number
    // this library seems buggy - needs to be processed
    // twice to actually read in country correctly
    const asYouType = new AsYouType("US");
    asYouType.input(newNumber);

    let newCountry;
    const newNumberObj = asYouType.getNumber();
    if (newNumberObj) {
      newCountry = newNumberObj.country || oldCountry;
      if (newNumberObj.isPossible()) {
        newNumber = newNumberObj.format("INTERNATIONAL");
      } else {
        newNumber = formatIncompletePhoneNumber(
          newNumberObj.number,
          newCountry,
        );
      }
    } else {
      newCountry = oldCountry;
    }

    return {
      number: newNumber,
      country: newCountry,
    };
  };

  const handleNumberChange = (e, fieldName) => {
    // detect new number and update field
    const newValue = {
      number: e.currentTarget.value,
      country: values[fieldName].country,
    };
    const newValueProcessed = processPhoneNumberInput(
      newValue,
      values[fieldName],
    );
    setFieldValue(fieldName, newValueProcessed);
  };

  return (
    <form onSubmit={submitForm}>
      {inputFields.map((field, index) => {
        if (field.type === "phoneNumber") {
          return (
            <EnhancedPhoneInput
              autoFocus={index === 0}
              name={field.name}
              placeholder={field.placeholder}
              onBlur={handleBlur}
              onChange={(e) => handleNumberChange(e, field.name)}
              value={values[field.name].number}
              country={values[field.name].country}
              countryOptions={phoneMetadata.countries}
              error={errors[field.name]}
              touched={touched[field.name]}
              key={`key-${field.name}`}
              size={PhoneInputSize.MEDIUM}
            />
          );
        } else {
          return (
            <EnhancedInput
              autoFocus={index === 0}
              type="text"
              name={field.name}
              placeholder={field.placeholder}
              onBlur={handleBlur}
              onChange={handleChange}
              value={values[field.name]}
              error={errors[field.name]}
              touched={touched[field.name]}
              key={`key-${field.name}`}
              size={Size.MEDIUM}
            />
          );
        }
      })}
      <EnhancedTextArea
        type="text"
        name="message"
        placeholder={
          "Message (optional) \n\nYour survey answers will be sent to the partner, but feel free to add more context."
        }
        onBlur={handleBlur}
        onChange={handleChange}
        value={values.message}
        error={errors.message}
        touched={touched.message}
        key="message"
        size={TextAreaSize.MEDIUM}
      />
      {!!failedSubmission && (
        <Error>
          Your email could not be delivered. Please try again, and contact
          support if the issue persists.
        </Error>
      )}
      <SubmitButton
        type="submit"
        disabled={isEmpty(touched) || !isEmpty(errors) || isSubmitting}
      >
        SEND MESSAGE
      </SubmitButton>
    </form>
  );
};

const formSchema = Yup.object().shape({
  fullName: Yup.string().required("Required"),
  workEmail: Yup.string()
    .email("Email must be valid")
    .required("Required"),
  companyName: Yup.string().required("Required"),
  phoneNumber: Yup.object().test(
    "phone",
    "Phone number must be valid",
    (value) => {
      // if field is empty, pass since it's
      // optional
      if (value === undefined) {
        return true;
      } else if (value.number === undefined) {
        return true;
      } else if (value.number.length === 0) {
        return true;
      }
      try {
        // validate phone number if field is
        // populated
        const phoneNumber = parsePhoneNumberFromString(
          value.number,
          value.country,
        );
        return phoneNumber.isValid();
      } catch (error) {
        return false;
      }
    },
  ),
  industry: Yup.string(),
  message: Yup.string(),
});

const formConfig = {
  mapPropsToValues: () => ({
    fullName: "",
    workEmail: "",
    companyName: "",
    phoneNumber: {
      country: "US",
      number: "",
    },
    industry: "",
    message: "",
  }),
  validationSchema: formSchema,
  validateOnBlur: true,
  validateOnChange: true,
};

const ContactForm = compose(
  withFormik(formConfig),
  withHandlers({
    getRequestBodyData: ({ values, questions, partner }) => () => ({
      recipient: {
        email: partner.referralEmailAddress,
        name: partner.name,
      },
      sender: { email: "portalreferrals@brex.com", name: "Brex Partners" },
      subject: "Referral from Brexs Partner Portal",
      emailBody: constructEmailBody(values, questions),
      replyTo: { email: values.workEmail, name: values.fullName },
    }),
  }),
  withPOSTFormSubmission({
    // TO DO: REPLACE THIS WITH APPROPRIATE LINKS ONCE https://github.com/brexhq/credit_card/pull/2088 LANDS
    submissionAddress: "https://referral-mailer-prod.brexapps.com/send-email",
  }),
)(_ContactForm);

const SuccessfulSubmission = ({ close, partner, stopPropagation }) => (
  <MessageContainer onClick={stopPropagation}>
    <QuitIcon src={Times} onClick={close} />
    <SubmissionIcon src={ThankYouIcon} />
    <Title>Thank you</Title>
    <Subtitle>{`We've sent your information to ${partner.name}`}</Subtitle>
    <QuitButton onClick={close}>Continue search</QuitButton>
  </MessageContainer>
);

const _MessageDialogue = ({
  partner,
  close,
  questions,
  submitForm,
  setHasSubmittedForm,
  stopPropagation,
}) => (
  <MessageContainer onClick={stopPropagation}>
    <QuitIcon src={Times} onClick={close} />
    <FormContainer>
      <Title>{`Send a message to ${partner.name}`}</Title>
      <ContactForm
        questions={questions}
        submitForm={submitForm}
        setHasSubmittedForm={setHasSubmittedForm}
        partner={partner}
      />
    </FormContainer>
  </MessageContainer>
);

const MessageDialogue = compose(
  withStateHandlers(
    { hasSubmittedForm: false },
    { setHasSubmittedForm: () => (value) => ({ hasSubmittedForm: value }) },
  ),
  withHandlers({
    handleClickOutside: ({ close }) => close,
    stopPropagation: () => (e) => {
      e.stopPropagation();
    },
  }),
  withClickOutside,
  branch(
    ({ hasSubmittedForm }) => !!hasSubmittedForm,
    renderComponent(({ partner, close, stopPropagation }) => (
      <SuccessfulSubmission
        stopPropagation={stopPropagation}
        close={close}
        partner={partner}
      />
    )),
  ),
)(_MessageDialogue);

const _ContactModal = ({ partner, open, close, isOpen, questions, button }) => (
  <React.Fragment>
    {isOpen &&
      ReactDOM.createPortal(
        <Modal>
          <MessageDialogue
            partner={partner}
            close={close}
            questions={questions}
          />
        </Modal>,
        document.body,
      )}
    {button(open, `${partner.name}-contact-button`)}
  </React.Fragment>
);

export default compose(
  withOpenClose(),
  withHandlers({
    closeOnEscape: ({ close }) => (e) => {
      if (e.code === "Escape") {
        close();
      }
    },
  }),
  lifecycle({
    componentDidMount() {
      if (this.props.isOpen) {
        document.body.className = "disabled-scroll-absolute";
      } else document.body.className = "";

      document.addEventListener("keydown", this.props.closeOnEscape);
    },
    componentWillUnmount() {
      document.removeEventListener("keydown", this.props.closeOnEscape);
    },
  }),
)(_ContactModal);
