import lunr from "lunr";
import * as ramda from "ramda";

const sort = (answers) => {
  if (answers.length < 5) return answers;

  const sortedAnswers = ramda.sortBy(
    ramda.compose(
      ramda.toLower,
      ramda.prop("displayTerm"),
      (answers) => answers.fields,
    ),
  )(answers);

  return sortedAnswers;
};

const processPartners = (partners) =>
  ramda.pipe(
    ramda.map((partner) => ({
      ...partner.fields,
      type: partner.fields.type.fields.category,
      logo: partner.fields.logo && partner.fields.logo.fields.file.url,
      answers: ramda.pipe(ramda.map((answer) => answer.fields.searchTerm))(
        partner.fields.answers,
      ),
    })),
    ramda.reduce(
      (partnerMap, currentPartner) => ({
        ...partnerMap,
        [currentPartner.id]: currentPartner,
      }),
      {},
    ),
  )(partners);

const processQuestions = (questions) => {
  // read selected question answers from query params
  const queryParams = new URLSearchParams(window.location.search);
  const queryParamsHash = {};
  const answersHash = {};
  const questionsToDelete = {};

  // sort queryParams by order
  const questionsSorted = Array.from(queryParams.keys())
    .map((questionId) => {
      const question = questions.find(
        (question) => question.fields.id === questionId,
      );
      if (question === undefined) {
        questionsToDelete[questionId] = true;
      }

      return question;
    })
    .filter((question) => question !== undefined)
    .sort(
      (question1, question2) => question1.fields.index - question2.fields.index,
    );

  // loop through query params, checking that answers are valid
  questionsSorted.forEach((question) => {
    const answerDependency = question.fields.answerDependency;
    const questionId = question.fields.id;
    if (answerDependency) {
      let hasDependency = false;

      answerDependency.forEach((answer) => {
        if (answer.fields.searchTerm in answersHash) {
          hasDependency = true;
        }
      });

      if (!hasDependency) {
        questionsToDelete[questionId] = true;
        return;
      }
    }

    const answerIndicesRaw = queryParams.get(questionId);
    if (answerIndicesRaw.length > 0) {
      const answerIndices = answerIndicesRaw.split(",");

      // if question does not accept multiple answers,
      // there should only be one index provided
      if (!question.fields.multipleAnswers) {
        if (answerIndices.length > 1) {
          questionsToDelete[questionId] = true;
          return;
        }
      }

      const questionHash = {};
      answerIndices.forEach((answerIndex) => {
        questionHash[answerIndex] = true;
        const answer = question.fields.answers[answerIndex];
        const answerId = answer.fields.searchTerm;
        answersHash[answerId] = true;
      });
      queryParamsHash[questionId] = questionHash;
    }
  });

  // can't delete within the forEach loop because that messes up
  // iteration
  Object.keys(questionsToDelete).forEach((key) => {
    queryParams.delete(key);
  });

  window.history.replaceState(
    null,
    "",
    `${window.location.pathname}?${queryParams}${window.location.hash}`,
  );

  return ramda.map((question) => ({
    ...question.fields,
    answerDependencySearchTerms:
      !!question.fields.answerDependency &&
      question.fields.answerDependency.map(
        (answer) => answer.fields.searchTerm,
      ),
    answers: sort(question.fields.answers, question.fields.index).map(
      (answer, answerIndex) => {
        let selected = false;
        const questionId = question.fields.id;

        if (questionId in queryParamsHash) {
          const questionHash = queryParamsHash[questionId];
          if (answerIndex in questionHash) {
            selected = true;
          }
        }
        return {
          ...answer.fields,
          selected,
          timestamp: null,
        };
      },
    ),
  }))(questions);
};

export const getPartners = async (client) => {
  const entries = await client.getEntries({
    content_type: "partner",
    "fields.type.sys.contentType.sys.id": "partnerType",
    "fields.type.fields.category": "Agency",
  });

  return processPartners(entries.items);
};

export const getQuestions = async (client) => {
  const entries = await client.getEntries({
    "sys.contentType.sys.id": "partnerType",
    include: 2,
  });
  const agency = entries.items.filter(
    (item) => item.fields.category === "Agency",
  )[0];

  const questions = agency.fields.questions;
  const processed = processQuestions(questions);
  return processed;
};

export const applyCustomSearchLogic = (results, partners) => {
  const customResults = results.map((result) => {
    let boost = 0;
    const tier = partners[result.ref].tier.toLowerCase();
    if (tier === "elite") {
      boost = 0.15;
    }
    if (tier === "premium") {
      boost += 0.05;
    }
    return { ...result, score: result.score + boost };
  });

  customResults.sort((a, b) => b.score - a.score);

  return customResults;
};

export const indexSearchEngine = (partners) =>
  lunr(function _() {
    this.ref("id");
    this.field("answers");
    partners.forEach((doc) => {
      const lunarDoc = { ...doc, answers: doc.answers.join(" ") };
      this.add(lunarDoc);
    }, this);
  });
