import styled from "@emotion/styled";
import colors from "styles/colors";

export const Size = {
  LARGE: {
    inputFontSize: 14,
    errorFontSize: 12,
    height: 60,
    marginBottom: 16,
  },
  MEDIUM: {
    inputFontSize: 12,
    errorFontSize: 10,
    height: 40,
    marginBottom: 8,
  },
};

export const Input = styled("input")(
  ({ fontSize, height, error, marginBottom }) => [
    {
      width: "100%",
      height,
      paddingLeft: 10,
      fontSize,
      marginBottom,
      border: `1px solid ${colors.lightGray}`,
      borderRadius: 0,
      outlineRadius: 0,
      fontWeight: 500,
      "&::placeholder": {
        color: colors.black,
        fontWeight: 300,
      },
      "&:focus": {
        borderColor: colors.gray1,
        outlineColor: colors.gray1,
        outlineStyle: "solid",
        outlineWidth: 1,
      },
    },
    error && {
      border: `1px solid ${colors.warning}`,
      "&:focus": {
        borderColor: colors.warning,
        outlineColor: colors.warning,
        outlineStyle: "solid",
        outlineWidth: 1,
      },
    },
  ],
);

export const InputError = styled("span")(({ size }) => ({
  position: "absolute",
  fontSize: size.errorFontSize,
  color: colors.warning,
  top: 0,
  right: 0,
  height: size.height,
  paddingRight: 10,
  display: "flex",
  alignItems: "center",
}));

export const InputWrapper = styled("div")(({ type }) => [
  {
    position: "relative",
    width: "100%",
  },

  type === "hidden" && {
    display: "none",
  },
]);
