import React from "react";

import { InputError, InputWrapper, Input, Size } from "./elements";

export { Size } from "./elements";

const EnhancedInput = ({
  autoFocus,
  type,
  name,
  placeholder,
  onBlur,
  onChange,
  value,
  error,
  touched,
  id,
  size = Size.LARGE,
}) => (
  <InputWrapper type={type}>
    <Input
      type={type}
      name={name}
      placeholder={placeholder}
      onBlur={onBlur}
      onChange={onChange}
      value={value}
      error={touched && error}
      id={id}
      fontSize={size.inputFontSize}
      height={size.height}
      marginBottom={size.marginBottom}
      autoFocus={autoFocus}
    />
    {!!error && touched && <InputError size={size}>{error}</InputError>}
  </InputWrapper>
);

export default EnhancedInput;
