import React from "react";
import { navigate } from "gatsby";

import { createBody } from "utils/contentful";
import { find, pipe, filter, map, propEq } from "ramda";
import Layout from "components/layout";
import pin from "assets/img/icons/black/light-fill/map-pin.svg";
import globe from "assets/img/icons/black/light-fill/globe.svg";

import bigcommerce from "assets/img/logos/not-brex/color/bigcommerce-tiny.png";
import magento from "assets/img/logos/not-brex/color/magento-tiny.png";
import shopify from "assets/img/logos/not-brex/color/shopify-tiny.png";
import woocommerce from "assets/img/logos/not-brex/color/woocommerce-tiny.png";
import wordpress from "assets/img/logos/not-brex/color/wordpress-tiny.png";
import hubspot from "assets/img/logos/not-brex/color/hubspot-tiny.png";
import salesforce from "assets/img/logos/not-brex/color/salesforce-tiny.png";

import fb from "assets/img/icons/logos/fb.svg";
import linkedin from "assets/img/icons/logos/linkedin.svg";
import twitter from "assets/img/icons/logos/twitter.svg";
import elite from "assets/img/partners/accounting/elite.svg";
import premium from "assets/img/partners/accounting/premium.svg";
import starter from "assets/img/partners/accounting/starter.svg";
import arrowUp from "assets/img/icons/white/arrow-up-2.svg";
import Sidebar from "./Sidebar";
import MainDetails from "./MainDetails";

import {
  ProfileWrapper,
  MainWrapper,
  BackButtonWrapper,
  BackButtonClickable,
  BackButtonTitle,
  PartnerName,
  PartnerNameWrapper,
  SidebarWrapper,
  WebsiteLink,
  Icon,
} from "./elements";

export const addStatusIcon = (status) => {
  const tier = status.toLowerCase();
  switch (tier) {
    case "elite":
      return elite;
    case "premium":
      return premium;
    default:
      return starter;
  }
};

const bulletData = {
  icons: {
    bigcommerce,
    magento,
    shopify,
    woocommerce,
    wordpress,
    hubspot,
    salesforce,
  },
  adjustments: {
    salesforce: {
      height: 21,
    },
    woocommerce: {
      height: 18,
    },
  },
};

const addAddresses = (partner) => {
  const structuredAddresses = [];
  const keys = [
    "additionalAddress1",
    "additionalAddress2",
    "additionalAddress3",
    "additionalAddress4",
  ];

  keys.forEach((address) => {
    if (partner[address]) {
      structuredAddresses.push({
        key: address,
        value: createBody(partner[address]),
      });
    }
  });

  if (structuredAddresses.length === 0) {
    structuredAddresses.push({
      key: "hqAddress",
      value: createBody(partner.hqAddress),
    });
  }

  return structuredAddresses;
};

const addSocialIcons = (facebookPage, linkedinProfile, twitterProfile) => {
  const imageIcons = [fb, linkedin, twitter];

  return [facebookPage, linkedinProfile, twitterProfile].reduce(
    (icons, link, index) => {
      if (link) {
        icons.push({
          link,
          imageSource: imageIcons[index],
        });
      }

      return icons;
    },
    [],
  );
};

// looks through answers array for specific matching id
// and then filters for matching answers with selected partner
// and maps out display name
const getSpecificAnswers = (questions, partnerAnswers, questionId) =>
  pipe(
    find(propEq("id", questionId)),
    (question) => question.answers,
    filter((answer) => partnerAnswers.indexOf(answer.searchTerm) > -1),
    map((answer) => ({
      key: answer.searchTerm,
      value: answer.displayTerm,
    })),
  )(questions);

const AgencyProfile = ({ pageContext: { partner, questions }, location }) => {
  const {
    answers,
    facebookPage,
    linkedinProfile,
    twitterProfile,
    hqAddress,
    detailedDescription,
    name,
    industriesServed,
    website,
    tier,
    tierReward = "",
  } = partner;

  const sidebarDescriptions = [
    {
      key: "sidebar1",
      icon: addStatusIcon(tier),
      title: <b>{`${tier.toUpperCase()} PARTNER`}</b>,
      description: `${tier.toLowerCase() === "elite" ? tierReward : ""}`,
    },
    {
      key: "sidebar2",
      icon: pin,
      title: "Headquarters",
      description: createBody(hqAddress),
    },
    {
      key: "sidebar3",
      icon: globe,
      title: (
        <WebsiteLink
          onClick={() => {
            if (window.analytics) {
              window.analytics.track("Click: Partner Outbound Link", {
                url: website,
              });
            }
          }}
          href={website}
          target="_blank"
          rel="noopener noreferrer"
        >
          Website
        </WebsiteLink>
      ),
    },
  ];

  const socialIcons = addSocialIcons(
    facebookPage,
    linkedinProfile,
    twitterProfile,
  );

  const processedQuestions = questions.map((question) => ({
    ...question,
    answers: question.answers.map((answer) => answer.fields),
  }));
  const processedAnswers = answers.map((answer) => answer.fields.searchTerm);
  const services = getSpecificAnswers(
    processedQuestions,
    processedAnswers,
    "agencyServiceProvided",
  );
  const software = getSpecificAnswers(
    processedQuestions,
    processedAnswers,
    "agencySoftware",
  );

  const bullets = software.map((bullet) => {
    const label = bullet.value.toLowerCase();
    const src = bulletData.icons[label];
    return src
      ? {
          src,
          ...bulletData.adjustments[label],
        }
      : null;
  });

  const detailedPoints = [
    {
      title: "Services offered",
      points: services,
    },
    {
      title: "Platform expertise",
      points: software,
      bullets,
    },
    {
      title: "Industries served",
      points: industriesServed.map((industry) => ({
        key: industry,
        value: industry,
      })),
    },
    {
      title: "Locations",
      points: addAddresses(partner),
    },
  ];

  const handleClick = () => {
    navigate(
      `/portal/agency?${new URLSearchParams(window.location.search)}${
        window.location.hash
      }`,
    );
  };

  return (
    <Layout
      title={`${name}, a Brex Agency Partner | Brex`}
      location={location}
      whiteContentBackground
      autoScrollBehavior
      hideDefaultFooter
    >
      <BackButtonWrapper>
        <BackButtonClickable onClick={handleClick}>
          <Icon src={arrowUp} />
          <BackButtonTitle>BACK TO SEARCH RESULTS</BackButtonTitle>
        </BackButtonClickable>
      </BackButtonWrapper>
      <PartnerNameWrapper>
        <PartnerName>{name}</PartnerName>
      </PartnerNameWrapper>
      <ProfileWrapper>
        <SidebarWrapper>
          <Sidebar
            partner={partner}
            socialIcons={socialIcons}
            sidebarDescriptions={sidebarDescriptions}
            questions={questions}
          />
        </SidebarWrapper>
        <MainWrapper>
          <MainDetails
            detailedDescription={detailedDescription}
            detailedPoints={detailedPoints}
          />
        </MainWrapper>
      </ProfileWrapper>
    </Layout>
  );
};

export default AgencyProfile;
