import React from "react";

import OutboundLink from "components/reusable/atoms/links/OutboundLink";
import ContactModal from "components/portal/ContactModal";
import SidebarDescription from "../SidebarDescription";

import {
  SocialIconContainer,
  Icon,
  ImageWrapper,
  PartnerImage,
  Button,
  StickyContainer,
} from "./elements";

const Sidebar = ({ partner, socialIcons, sidebarDescriptions, questions }) => (
  <React.Fragment>
    <ImageWrapper>
      <PartnerImage src={partner.logo.fields.file.url} />
    </ImageWrapper>
    <StickyContainer>
      <ContactModal
        partner={partner}
        questions={questions}
        button={(clickHandler, id) => (
          <Button onClick={clickHandler} id={id}>
            Contact partner
          </Button>
        )}
      />
      {sidebarDescriptions.map((description) => (
        <SidebarDescription
          key={`${partner.name}-${description.key}`}
          icon={description.icon}
          title={description.title}
          description={description.description}
        />
      ))}
      <SocialIconContainer>
        {socialIcons.map((icon) => (
          <OutboundLink key={icon.link} href={icon.link} target="_blank">
            <Icon src={icon.imageSource} />
          </OutboundLink>
        ))}
      </SocialIconContainer>
    </StickyContainer>
  </React.Fragment>
);

export default Sidebar;
