import React from "react";
import { withHandlers } from "recompose";
import { navigate } from "gatsby";

import { createBody } from "utils/contentful";
import pin from "assets/img/icons/black/light-fill/map-pin.svg";
import arrow from "assets/img/portal/arrow.svg";
import ContactModal from "components/portal/ContactModal";
import { addStatusIcon } from "components/partner-profile-page/agencyProfileTemplate";
import { formatKebabCase } from "utils/formatting";

import {
  Wrapper,
  LeftContainer,
  ImageWrapper,
  Image,
  RightContainer,
  Address,
  Name,
  Status,
  StatusIcon,
  FooterWrapper,
  AddressIcon,
  AddressWrapper,
  Description,
  ArrowIcon,
  ImagePlaceholder,
  ContactButton,
  TierReward,
} from "./elements";

const PartnerCard = ({ partner, selectPartner, questions }) => (
  <Wrapper
    onClick={selectPartner}
    starterStatus={partner.tier.toLowerCase() === "starter"}
  >
    <LeftContainer>
      {partner.logo ? (
        <ImageWrapper>
          <Image src={partner.logo} />
        </ImageWrapper>
      ) : (
        <ImagePlaceholder>LOGO</ImagePlaceholder>
      )}
      <ContactModal
        partner={partner}
        questions={questions}
        button={(clickHandler, id) => (
          <ContactButton onClick={clickHandler} id={id}>
            Contact partner
          </ContactButton>
        )}
      />
    </LeftContainer>
    <RightContainer>
      <Name>{partner.name}</Name>
      <AddressWrapper>
        <AddressIcon src={pin} />
        <Address>
          {!!partner.hqAddress && createBody(partner.hqAddress)}
        </Address>
      </AddressWrapper>
      <Description>{partner.description}</Description>
      <FooterWrapper>
        <Status>
          <StatusIcon src={addStatusIcon(partner.tier)} />
          {`${partner.tier} PARTNER`}
        </Status>
        {partner.tier.toLowerCase() === "elite" && (
          <TierReward>{partner.tierReward}</TierReward>
        )}
      </FooterWrapper>
    </RightContainer>
    {partner.tier.toLowerCase() !== "starter" && <ArrowIcon src={arrow} />}
  </Wrapper>
);

export default withHandlers({
  selectPartner: ({ partner, partnerType }) => (e) => {
    const verticalPath = partnerType.split(" ")[0].toLowerCase();
    if (window.analytics) {
      const verticalPortal = `${verticalPath}Portal`;
      window.analytics.track("Click: Partner", {
        target: `${verticalPortal}.partnerCardClicked.${partner.name}`,
      });
    }
    if (
      partner.tier.toLowerCase() !== "starter" &&
      e.target.id !== `${partner.name}-contact-button`
    ) {
      const path = `/portal/${verticalPath}/${formatKebabCase(
        partner.id,
      )}?${new URLSearchParams(window.location.search)}${window.location.hash}`;
      navigate(path);
    }
  },
})(PartnerCard);
