import styled from "@emotion/styled";
import colors from "styles/colors";

export const Size = {
  LARGE: {
    inputFontSize: 14,
    errorFontSize: 12,
  },
  MEDIUM: {
    inputFontSize: 12,
    errorFontSize: 10,
  },
};

export const TextArea = styled("textarea")(({ fontSize, error }) => [
  {
    width: "100%",
    height: 100,
    marginBottom: 16,
    paddingLeft: 10,
    fontSize,
    paddingTop: 14,
    paddingBottom: 14,
    border: `1px solid ${colors.lightGray}`,
    borderRadius: 0,
    outlineRadius: 0,
    fontWeight: 500,
    "&::placeholder": {
      color: colors.black,
      fontWeight: 300,
    },
    "&:focus": {
      borderColor: colors.gray1,
      outlineColor: colors.gray1,
      outlineStyle: "solid",
      outlineWidth: 1,
    },
  },
  error && {
    border: `1px solid ${colors.warning}`,
    "&:focus": {
      borderColor: colors.warning,
      outlineColor: colors.warning,
      outlineStyle: "solid",
      outlineWidth: 1,
    },
  },
]);

export const Error = styled("span")(({ fontSize }) => ({
  position: "absolute",
  fontSize,
  color: colors.warning,
  right: 0,
  paddingTop: 5,
  paddingRight: 10,
}));

export const Wrapper = styled("div")(({ type }) => [
  {
    position: "relative",
    width: "100%",
  },

  type === "hidden" && {
    display: "none",
  },
]);
