import React from "react";

import { withClickAnalytics } from "hocs/analytics";
import arrowDown from "assets/img/icons/black/light-fill/arrow-down.svg";

import {
  ControlBarContainer,
  ControlButton,
  _DoneButton,
  SelectedAnswersWrapper,
  ButtonTitle,
  ButtonIconContainer,
  ButtonIcon,
} from "./elements";

const DoneButton = withClickAnalytics(
  "Button",
  "accountingPortal.controlBar.doneButtonClicked",
)(_DoneButton);

const Button = ({
  title,
  inverted,
  invisible,
  disabled,
  handler,
  shouldRender = true,
}) =>
  shouldRender && (
    <ControlButton
      disabled={disabled}
      invisible={invisible}
      onClick={() => !disabled && handler()}
      inverted={inverted}
    >
      <ButtonTitle>{title}</ButtonTitle>
      <ButtonIconContainer disabled={disabled}>
        <ButtonIcon
          src={arrowDown}
          transform={inverted ? "rotate(90deg)" : "rotate(-90deg)"}
        />
      </ButtonIconContainer>
    </ControlButton>
  );

const ControlBar = ({
  isFinished,
  goToPreviousQuestion,
  currentQuestion,
  renderSelectedAnswers,
  questions,
  selectedAnswers,
  goToNextQuestion,
  setIsFinished,
}) => {
  const isNextButtonDisabled = (questions, currentQuestion) => {
    if (!questions[currentQuestion].requireAnswer) {
      return false;
    }
    return (
      !questions[currentQuestion].answers.filter((answer) => answer.selected)
        .length > 0
    );
  };

  return (
    <ControlBarContainer isFinished={isFinished}>
      <Button
        handler={goToPreviousQuestion}
        invisible={currentQuestion === 0 || isFinished}
        title="BACK"
        inverted
      />
      <SelectedAnswersWrapper>{renderSelectedAnswers()}</SelectedAnswersWrapper>
      {currentQuestion < questions.length - 1 ? (
        <Button
          handler={goToNextQuestion}
          disabled={isNextButtonDisabled(questions, currentQuestion)}
          title="NEXT"
          shouldRender={selectedAnswers.length > 0}
        />
      ) : (
        <DoneButton
          isFinished={isFinished}
          onClick={() => {
            if (isFinished) {
              window.scrollTo(0, 0);
            }
            setIsFinished(!isFinished);
          }}
        >
          <ButtonTitle isFinished={isFinished}>
            {isFinished ? "Modify my answers" : "DONE"}
          </ButtonTitle>
          <ButtonIconContainer>
            <ButtonIcon
              src={arrowDown}
              transform={isFinished ? "rotate(0deg)" : "rotate(180deg)"}
            />
          </ButtonIconContainer>
        </DoneButton>
      )}
    </ControlBarContainer>
  );
};

export default ControlBar;
