import { compose, withHandlers, withStateHandlers } from "recompose";

const withPOSTFormSubmission = ({ submissionAddress }) =>
  compose(
    withStateHandlers(
      {
        successfulSubmission: false,
        failedSubmission: false,
      },
      {
        setSubmissionSuccess: () => (wasSuccessful) => ({
          successfulSubmission: wasSuccessful,
          failedSubmission: !wasSuccessful,
        }),
      },
    ),
    withHandlers({
      submitForm: ({ setSubmissionSuccess, getRequestBodyData }) => (e) => {
        e.preventDefault();
        fetch(submissionAddress, {
          method: "POST",
          body: JSON.stringify(getRequestBodyData()),
          headers: new Headers({
            "Content-Type": "application/json",
          }),
        }).then(
          ({ status }) => setSubmissionSuccess(status === 200),
          () => setSubmissionSuccess(false),
        );
      },
    }),
  );

export default withPOSTFormSubmission;
