import React from "react";

import { Error, Wrapper, TextArea, Size } from "./elements";

export { Size } from "./elements";

const EnhancedInput = ({
  type,
  name,
  placeholder,
  onBlur,
  onChange,
  value,
  error,
  touched,
  id,
  size = Size.LARGE,
}) => (
  <Wrapper type={type}>
    <TextArea
      type={type}
      name={name}
      placeholder={placeholder}
      onBlur={onBlur}
      onChange={onChange}
      value={value}
      error={touched && error}
      id={id}
      fontSize={size.inputFontSize}
    />
    {!!error && touched && <Error fontSize={size.errorFontSize}>{error}</Error>}
  </Wrapper>
);

export default EnhancedInput;
