import React from "react";
import { range } from "lodash";

import {
  Container,
  LeftContent,
  QuestionWrapper,
  QuestionText,
  ProgressBarWrapper,
  ProgressBar,
  RightContent,
  AnswersWrapper,
  Answer,
  SelectedAnswersWrapperMobile,
  Subtitle,
} from "./elements";

import { getAnswersForQuestion } from "./helpers";

const renderAnswers = (
  questions,
  currentQuestionIndex,
  handleAnswerSelection,
  shortAnswerSet,
  multipleAnswers,
) => {
  const currentQuestion = questions[currentQuestionIndex];
  const answers = getAnswersForQuestion(currentQuestion);

  return answers.map((answer, index) => (
    <Answer
      key={answer.searchTerm}
      selected={questions[currentQuestionIndex].answers[index].selected}
      onClick={() => handleAnswerSelection(currentQuestionIndex, index)}
      long={shortAnswerSet}
    >
      {multipleAnswers && index === 0 && (
        <Subtitle>Select all that apply</Subtitle>
      )}
      {answer.displayTerm}
    </Answer>
  ));
};

const QuestionPlatform = ({
  isFinished,
  questions,
  currentQuestion,
  handleAnswerSelection,
  multipleAnswers,
  renderSelectedAnswers,
}) => {
  const shortAnswerSet =
    !!questions[currentQuestion] &&
    questions[currentQuestion].answers.length < 6;

  return (
    <Container isFinished={isFinished}>
      <LeftContent>
        <QuestionWrapper>
          <QuestionText>
            <ProgressBarWrapper>
              {range(5).map((_, index) => (
                <ProgressBar
                  key={`bar-${_}`}
                  active={questions[currentQuestion].index === index}
                />
              ))}
            </ProgressBarWrapper>
            {questions[currentQuestion].customerCopy}
          </QuestionText>
        </QuestionWrapper>
      </LeftContent>
      <RightContent>
        <AnswersWrapper
          fullHeight={shortAnswerSet}
          multipleAnswers={multipleAnswers}
        >
          {renderAnswers(
            questions,
            currentQuestion,
            handleAnswerSelection,
            shortAnswerSet,
            multipleAnswers,
          )}
          {!shortAnswerSet &&
            range(4).map((_) => <Answer key={`filler-${_}`} filler />)}
        </AnswersWrapper>
      </RightContent>
      <SelectedAnswersWrapperMobile>
        {renderSelectedAnswers()}
      </SelectedAnswersWrapperMobile>
    </Container>
  );
};

export default QuestionPlatform;
