interface IQuestion {
  customerCopy: string;
  answers: IAnswer[];
  id: string;
  multipleAnswers: boolean;
  requireAnswer: boolean;
  index: number;
  answerDependencySearchTerms: boolean;
}

interface IAnswer {
  searchTerm: string;
  displayTerm: string;
  requiredMatch: boolean;
  weight: number;
  selected: boolean;
  timestamp: null;
  index?: number;
}

export const getAnswersForQuestion = (question: IQuestion): IAnswer[] => {
  return question.answers.sort((a, b) => {
    if (a.index !== undefined && b.index !== undefined)
      return a.index - b.index;
    else return 0;
  });
};
